













import { Component, Vue } from "vue-property-decorator";

@Component
export default class Random extends Vue {
  mounted() {
    this.$store.dispatch("getRandomPic");
  }

  get randomPic() {
    return this.$store.state.randomPic;
  }

  private getAnotherPic() {
    this.$store.dispatch("getRandomPic");
  }
}
